
.b-markers {
  position: relative;
}
.b-markers-letters, .b-markers-numbers {
    display: block;
    padding: 0;
    margin: 0;
}
.b-markers-letters li, .b-markers-numbers li {
      display: block;
      list-style: none;
      padding: 0;
      margin: 0;
      width: 36px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      text-transform: uppercase;
      font-size: 12px;
}
.b-markers-letters {
    position: absolute;
    left: 30px;
    top: -8px;
}
.b-markers-letters li {
      float: left;
}
.b-markers-numbers {
    position: absolute;
    left: -9px;
    top: 30px;
}



