
body {
  color: #2c3e50;
  min-width: 920px;
}
.b-container {
  max-width: 880px;
  margin: 0 auto;
}
.b-canvas-wrapper {
  width: 400px;
  height: 400px;
  margin-bottom: 2em;
}
.b-canvas-wrapper > svg {
    overflow: visible !important;
}
.b-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 -15px;
}
.b-col {
  float: left;
  padding: 0 15px;
}
.b-col-left {
    width: 50%;
}
.b-col-right {
    width: 50%;
}
.b-logo {
  margin-bottom: 2em;
  text-align: center;
}
.b-svg-ship {
  stroke: #00f;
  fill: rgba(0, 0, 255, 0.05);
  rx: 0;
  ry: 0;
  stroke-width: 2px;
  cursor: pointer;
  shape-rendering: crispEdges;
}
.b-svg-ship--moveble {
    stroke: #05e500;
    fill: rgba(30, 255, 0, 0.1);
}
.b-svg-grid-item {
  fill: #fff;
  stroke: #B4B4FF;
  stroke-width: 1px;
  shape-rendering: crispEdges;
}
.b-svg-grid-item--rival {
    cursor: pointer;
}
.b-svg-grid-item--rival:hover {
      fill: rgba(30, 255, 0, 0.24);
}
.b-svg-grid-item--miss {
    fill: #e9ebef;
}
.b-grid {
  position: relative;
}
.b-grid-table {
    margin: 0;
    border-collapse: collapse;
    cursor: default;
    display: block;
    width: 400px;
    height: 400px;
    position: absolute;
    left: 29px;
    top: 29px;
}
.b-grid-item {
    border: 1px solid #B4B4FF;
    padding: 0;
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: absolute;
}
.b-grid-item-content {
      position: relative;
      height: 35px;
      width: 35px;
      font-size: 12px;
}
.b-grid-item--empty:hover .b-grid-item-content:after {
      border: 2px solid #40BF44;
      background: rgba(64, 191, 68, 0.05);
      position: absolute;
      width: 35px;
      height: 35px;
      top: 0;
      left: 0;
      margin: -2px;
      content: "";
      display: block;
      cursor: pointer;
      z-index: 2;
      -webkit-box-sizing: content-box;
              box-sizing: content-box;
}
.b-grid-item--miss .b-grid-item-content {
      background-color: #f2f4f8;
}
.b-grid-item--miss .b-grid-item-status {
      position: absolute;
      top: 50%;
      left: 50%;
      height: 4px;
      width: 4px;
      background: #333;
      border-radius: 50%;
      margin-top: -2px;
      margin-left: -2px;
}
.b-grid-item--miss-auto .b-grid-item-status {
      background: silver;
}
.b-grid-item--hit .b-grid-item-content {
      background: 0 0;
      position: relative;
}
.b-grid-item--hit .b-grid-item-status {
      height: 100%;
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      overflow: hidden;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      background: none;
      margin-left: 0;
      margin-top: 0;
      border-radius: 0;
}
.b-grid-item--hit .b-grid-item-status:before, .b-grid-item--hit .b-grid-item-status:after {
        content: "";
        position: absolute;
        z-index: -1;
        background: red;
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
}
.b-grid-item--hit .b-grid-item-status:before {
        left: 50%;
        width: 1px;
        top: -25%;
        height: 150%;
        margin-top: 1px;
}
.b-grid-item--hit .b-grid-item-status:after {
        top: 50%;
        height: 1px;
        left: -25%;
        width: 150%;
        margin-left: -1px;
}
.b-grid-item--done .b-grid-item-content:after {
      content: "";
      display: block;
      height: 37px;
      width: 37px;
      border: 1px solid red;
      margin: -1px;
      position: absolute;
      z-index: 3;
      top: 0;
      left: 0;
}
.b-grid-item--x-0 {
      left: 0px;
}
.b-grid-item--x-1 {
      left: 36px;
}
.b-grid-item--x-2 {
      left: 72px;
}
