      top: 324px;
}
.b-ship {
  z-index: 2;
  left: 0;
  top: 0;
  position: absolute !important;
  margin: -2px;
  border: 2px solid;
  width: 35px;
  height: 35px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  border-color: #00f;
  background: rgba(0, 0, 255, 0.05);
}
.b-ship--done {
    border-color: red;
    background: rgba(255, 0, 0, 0.05);
}
